<template>
  <div class="personaldata">
    <hx-Return :title="title"></hx-Return>
    <div class="head-portrait">
      <span>头像</span>
      <img src="../../assets/images/usermy.png" alt />
    </div>
    <van-cell-group>
      <van-field v-model="myname" label="用户名" @blur="edit" />
      <van-cell-group>
        <van-field label="出生日期" :value="value" readonly @click="show = true" />
      </van-cell-group>
    </van-cell-group>
    <van-popup v-model="show" position="bottom" :style="{ height: '50%' }" >
        <van-datetime-picker
          v-model="currentDate"
          type="date"
          title="选择年月日"
          :min-date="minDate"
          :max-date="maxDate"
          @confirm="confirmPicker"
          @cancel="cancelPicker"
        />
    </van-popup>
  </div>
</template>

<script>
import hxReturn from "../../components/hxReturn";

export default {
  data() {
    return {
      title: "个人信息",
      myname: "11",
      Myname: "",
      minDate: new Date(1550, 0, 1),
      maxDate: new Date(2020, 6, 1),
      currentDate: new Date(),
      show: false,
      value: ""
    };
  },
  methods: {
    edit() {
      if (this.myname.length < 2) {
        this.$toast("用户名最少二位数");
        this.myname = this.Myname;
      }else{
        this.$store.state.username = this.myname
      }
    },
    confirmPicker(value) {
      let year = value.getFullYear();
      let month = value.getMonth() + 1;
      let day = value.getDate();
      this.value = year + "年" + month + "月" + day + "日";
      this.$store.state.dataTime = this.value;
      this.show = false
    },
    cancelPicker(){
      this.show = false
    }
  },
  created() {
    this.$store.state.vantTab = false;
    this.myname = this.$store.state.username
    this.Myname = this.myname;
    this.value = this.$store.state.dataTime
  },
  components: {
    hxReturn
  }
};
</script>

<style lang="less" scoped>
.personaldata {
  padding-top: 50px;
  .head-portrait {
    padding: 10px;
    line-height: 60px;
    background-color: #ffffff;
    margin-top: 20px;
    margin-bottom: 10px;
    img {
      width: 40px;
      height: 40px;
      position: relative;
      top: 10px;
      left: 40px;
    }
  }
}
</style>